:root {
  --primary-muted: rgb(247, 247, 247);
  --secondary-blue: rgb(8, 46, 104);
  --vivax-yellow: rgb(255, 206, 50);
}

body {
  margin: 0;
  background-color: var(--primary-muted);
}

input[type="text" i] {
  padding: 0;
  border-width: 0;
}

/* Autocomplete Classes */
/* .autocomplete {
  position: relative;
} */

.autocomplete-items {
  position: absolute;
  border: 1px solid #d4d4d4;
  border-bottom: none;
  border-top: none;
  z-index: 99;
  top: 100%;
  left: 0;
  right: 0;
}
.autocomplete-items div {
  padding: 10px;
  cursor: pointer;
  background-color: #fff;
  border-bottom: 1px solid #d4d4d4;
}
.autocomplete-items div:hover {
  background-color: #e9e9e9;
}
.autocomplete-active {
  background-color: DodgerBlue !important;
  color: #ffffff;
}

.page_outline {
  height: 100vh;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

.map_container {
  height: 80vh;
  width: 70vw;
  border-radius: 20px;
  border: var(--secondary-blue) 2px solid;
}

.sidebar_container {
  height: 80vh;
  width: 15vw;
  background-color: var(--secondary-blue);
  border-radius: 20px;
  border: black 2px solid;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.color_sample {
  display: inline-block;
  margin-left: 2px;
  height: 10px;
  width: 10px;
  border: black 1px solid;
  background-color: gray;
}

.color_names {
  display: inline;
  margin: 2px 0;
}

.section_header {
  color: var(--primary-muted);
  text-align: center;
}

.section_half_field {
  width: 45%;
  margin: 5px 0;
}

.section_full_field {
  width: 90%;
  margin: 5px 0;
}

.section_button {
  width: 80%;
  margin-top: 5px;
  border-radius: 5px;
  border: var(--secondary-blue) 1px solid;
}

.section_flex {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.width_100 {
  width: 100%;
}

.txt_wht {
  color: var(--primary-muted);
  text-align: center;
}

.custom_link:hover {
  text-decoration: underline;
  cursor: pointer;
}

.collapse_btn {
  display: none;
}

@media screen and (max-width: 1080px) {
  .page_outline {
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
  }

  .sidebar_container {
    height: auto;
    width: 90vw;
    background-color: var(--secondary-blue);
    flex-direction: row;
    justify-content: space-evenly;
  }

  .map_container {
    height: 80vh;
    width: 100vw;
    border-radius: 0px;
    border: var(--secondary-blue) 0px solid;
  }

  .form_collapse {
    display: none;
  }

  .collapse_btn {
    display: inline;
  }
}
